  color: #fff;
  font-family: 'Segoe UI';
  font-size: medium;
  left: 0;
  line-height: 45px;
  opacity: .75;
  position: fixed;
  right: 0;
  text-align: center;
  top: 0;
  z-index: 999999999;
}

.sf-license-close-btn {
  background: url("data:image/svg+xml;base64,");
  cursor: pointer;
  height: 20px;
  margin-top: 14px;
  padding-top: 10px;
  position: absolute;
  right: 20px;
  width: 20px;
}

.sf-license-warning a,
.sf-license-warning a:hover {
  color: #ffd800;
}

.e-error {
  color: #f44336;
  font-family: "Roboto", "Segoe UI", "GeezaPro", "DejaVu Serif", "sans-serif", "-apple-system", "BlinkMacSystemFont";
